.toc {
    width: 200px;
    float: left;
    padding: 20px;
    background-color: #f0f0f0;
    height: 100vh; /* Full-height container */
    overflow: auto; /* Enable scrolling if the page is too long */
}

.content {
    margin-left: 220px; /* Same as toc width plus some padding */
    padding: 20px;
    background-color: #fff;
    height: 100vh;
    overflow: auto;
}

.toc ul {
    list-style-type: none; /* Remove default bullets */
    padding: 0;
}

.toc li button {
    background: none;
    border: none;
    text-align: left;
    color: blue;
    cursor: pointer;
}

.toc li button:hover {
    text-decoration: underline;
}

.content {
    font-family: 'Arial', sans-serif; /* Improves readability */
}
